.page-footer {
  &--main-mobile {
    display: none;
  }
}

.page-footer__wrapper {
  padding-right: 20px;
  padding-left: 20px;
}

.page-footer__container {
  width: 180px;
  margin: 0 auto;
  padding-top: 39px;
  padding-bottom: 39px;
}

.page-footer__logo {
  display: block;
  width: 147px;
  height: 40px;
  margin-bottom: 43px;
  padding-left: 16px;
  color: #283645;
  cursor: pointer;

  &:hover {
    color: $color-pink;
  }

  &:active {
    color: #283645;
    opacity: 0.3;
  }
}

.page-footer__logotype-big {
  display: none;
}

.page-footer__copyright {
  margin-top: 35px;
}

@media (min-width: $tablet-width) {
  .page-footer {
    &--main-mobile {
      display: flex;
    }
  }

  .page-footer__container {
    display: flex;
    justify-content: space-between;
    width: 660px;
    padding: 20px;
  }

  .page-footer__logo {
    padding-top: 14px;
    padding-left: 0;
  }

  .page-footer__social-list {
    width: 195px;
    margin-top: 20px;
    padding-right: 19px;
  }

  .page-footer__copyright {
    margin-top: 0;
    padding-top: 27px;
  }
}

@media (min-width: $desktop-width) {
  .page-footer__container {
    width: 940px;
    padding: 85px 0;
  }

  .page-footer__logotype-small {
    display: none;
  }

  .page-footer__logotype-big {
    display: inline-block;
  }

  .page-footer__social-list {
    width: auto;
    padding-left: 35px;
  }
}
