.textarea {
  min-height: 200px;
  padding: 12px 20px;
  border: 2px solid #e5e5e5;
}

.textarea::-moz-placeholder {
  color: #283645;
  font-size: 18px;
  line-height: 30px;
}

.textarea::-webkit-input-placeholder {
  color: #283645;
  font-size: 18px;
  line-height: 30px;
}

.textarea:-ms-input-placeholder {
  color: #283645;
  font-size: 18px;
  line-height: 30px;
}

.textarea::-ms-input-placeholder {
  color: #283645;
  font-size: 18px;
  line-height: 30px;
}

.textarea::placeholder {
  color: #283645;
  font-size: 18px;
  line-height: 30px;
}
